.home-container {
  display: flex;
  justify-content: space-between;
  position: relative;
  width: 90vw;
  margin: auto;
}
.l-title {
  left: -100px;
  position: relative;
  width: 80px;
}

.h-leftdiv {
  width: 60vw;
  /* border: 1px solid red; */
text-align: start;
  color: var(--secondary-color);
}
.l-title {
    font-size: 30px;
    text-align: start;
    
  }
  .web-developer{
    position: relative;
    left: 100px;
  }
.h-left-name {
  /* border: 1px solid red; */
  width: 80%;
  top: 90px;
  left: 50px;
  position: relative;
  /* text-align: start; */
}

.my-name {
  color: var(--title-color);
  font-size: 65px;
