  left: 20px;
}

.cv-button{
    color: var(--secondary-color);
    top: 140px;
    position: relative;
    background-color:var(--title-color);
    border-radius: 20px;
    padding: 10px;
}
.social-icons{

top: 150px;
position: relative;
/* border: 1px solid red; */
display: flex;
width: 50%;
margin: auto;
justify-content: space-evenly;
}
.social-icons>a>img{
    width: 30px;
}

.h-rightdiv {
  width: 60vw;
  /* flex: 1; */
  /* border: 1px solid green; */
  position: relative;
  /* align-items: fl/ex-start; */
}

.hero-img {
  width: 24vw;
