}

.vector-blue{
    position: relative;
}
@media screen and (max-width: 480px) {

    .home-container {
        display: flex;
    flex-direction: column;
        position: relative;
        width: 100vw;
        margin: auto;
      }

      .my-name {
        /* color: var(--title-color); */
        font-size: 40px;
        position: relative;
        left: 10px;
      }
      .h-left-name {
        /* border: 1px solid red; */
        width: 80%;
        top: 20px;
        left: 20px;
        position: relative;
        /* text-align: start; */
      }
      .web-developer{
        position: relative;
        left: 100px;
        font-size: 14px;
      }
      .hero-img {
        width: 54vw;
        height: 38vh;
        position: relative;
        left: -20px;
        top:-80px;
      }
      .social-icons{

        top: 200px;
        left: 180px;
        position: relative;
        /* border: 1px solid red; */
        display: flex;
        width: 60%;
        margin: auto;
        justify-content: space-evenly;
        }
        .social-icons>a>img{
            width: 30px;
        }
        .cv-button{
            color: var(--secondary-color);
            top: 300px;
            position: relative;
            background-color:var(--title-color);
            border-radius: 20px;
            padding: 10px;
            left: 200px;
        }
}